// Überprüfung, ob der Name bereits gespeichert ist
var playerName = localStorage.getItem("playerName");

if (playerName === null || playerName === "") {
  // Wenn der Name leer ist oder noch nicht gespeichert wurde, zeige das Eingabefeld an
  document.body.innerHTML = `
    <!DOCTYPE html>
    <html>
    <head>
      <title>Würfelspiel</title>
      <style>
        body {
          text-align: center;
          font-family: Arial, sans-serif;
        }
      </style>
    </head>
    <body>
      <h1>Bitte gib deinen Namen ein:</h1>
      <input type="text" id="nameInput" />
      <button id="submitButton">Speichern</button>

      <script src="index.js"></script>
    </body>
    </html>
  `;

  // Ereignislistener für den Klick auf den Speichern-Button
  document
    .getElementById("submitButton")
    .addEventListener("click", function () {
      var nameInput = document.getElementById("nameInput");
      var playerName = nameInput.value.trim();

      if (playerName !== "") {
        // Speichern des Namens in localStorage
        localStorage.setItem("playerName", playerName);

        // Laden der Seite, um das eigentliche Spiel anzuzeigen
        location.reload();
      }
    });
} else {
  // Wenn der Name nicht leer ist, zeige das eigentliche Spiel an
  document.body.innerHTML = `
    <!DOCTYPE html>
    <html>
    <head>
      <title>Würfelspiel</title>
      <style>
        body {
          text-align: center;
          font-family: Arial, sans-serif;
        }
        #cocktailImages {
          display: flex;
          justify-content: center;
          margin-bottom: 20px;
        }

        #cocktailImages img {
          width: 70px;
        }
      </style>
    </head>
    <body>
      <img src="https://www.ibs-ol.de/files/projektordner/static/logo.png" />
      <h1>IBS-Trinkspiel</h1>
      <button id="rollButton">Würfeln</button>
      <div id="cocktailImages"></div>
<p id="infoText"/>
      <script src="index.js"></script>
    </body>
    </html>
  `;

  // Funktion zum Würfeln
  function rollDice() {
    document.getElementById("cocktailImages").innerHTML = "";
    var roll = Math.floor(Math.random() * 6) + 1;

    for (let index = 0; index < roll; index++) {
      var img = document.createElement("img");
      img.src =
        "https://st2.depositphotos.com/2197868/7830/v/950/depositphotos_78306752-stock-illustration-shot-glass-hand-drawn.jpg";

      document.getElementById("cocktailImages").appendChild(img);
    }

    // Anzeigen des Spielers und der Anzahl der Shots
    document.getElementById(
      "infoText"
    ).textContent = `Viel Spaß ${playerName} bei deinen ${roll} Shots`;
  }

  // Ereignislistener für den Klick auf den Würfeln-Button
  document.getElementById("rollButton").addEventListener("click", rollDice);
}
